import React from 'react'
import { graphql } from 'gatsby'
import './index.css'
import '../components/Navigation'
import Layout from '../components/Layout'

export default class Template extends React.Component {
  componentDidMount() {
    const imageWrappers = document.querySelectorAll('.image-row-wrapper')
    imageWrappers.forEach(wrapper => {
      const imageNode = wrapper.querySelector('img')
      wrapper.style.flex = `${imageNode.width / imageNode.height}`
    })
  }

  render() {
    const { markdownRemark } = this.props.data // data.markdownRemark holds our post data
    const { frontmatter, html } = markdownRemark
    return (
      <Layout>
        <div className="blog-post">
          <h1>{frontmatter.title}</h1>
          <h4>{frontmatter.date}</h4>
          <div
            className="blog-post-content"
            dangerouslySetInnerHTML={{ __html: html }}
          />
        </div>
      </Layout>
    )
  }
}

export const pageQuery = graphql`
  query($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        path
        title
      }
    }
  }
`
